import React from 'react';
import './sidebar.css';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';

const Sidebar = (props) => {
    return (<div class="Sidebar">
        <div onClick={props.clicked}>
            <ArrowBackIcon style={{ fontSize: 30 }}></ArrowBackIcon>
        </div>
    </div>);
};

export default Sidebar;