import React from 'react';
import './activity.css';

const Activity = (props) => {
    return (<div className="Activity" onClick={() => props.select(props.index)}>
        <h2>{props.title}</h2>
        <p><strong>Timeframe: </strong> {props.timeframe}</p>
        <p><strong>Topics: </strong> {props.topics.map((topic, index) => {
            return index !== props.topics.length - 1 
                ? (<span>{topic}, </span>)
                : (<span>{topic}</span>)
        })}</p>
    </div>);
};

export default Activity;