import React, { useEffect, useState } from 'react';
import Activity from '../activity/activity';
import Question from '../question/question';
import './main.css';

const Main = (props) => {
    const [activities, setActivities] = useState([]);
    const [currentActivity, setCurrentActivity] = useState(null);

    useEffect(() => {
        const getData = async () => {
            const data = await fetch('activities.json');
            await setActivities((await data.json()).activities);
        };

        getData();
    }, []);

    useEffect(() => {
        if (props.onBack) {
            setCurrentActivity(null);
            props.clicked(false);
        }
    });

    const loadActivity = (newActivity) => {
        setCurrentActivity(activities[newActivity]);
    };

    return (<div className="Main">
        <header>
            <h1>{currentActivity ? currentActivity.title : "Activities"}</h1>
        </header>
        <main>
            {
                currentActivity
                ?   currentActivity.questions.map((question) => {
                    return (<Question {...question}></Question>);
                })
                :    activities.map((activity, index) => {
                        return(<Activity {...activity} index={index} select={loadActivity}></Activity>);
                    })
            }
        </main>
    </div>)
};

export default Main;