import React, { useEffect, useState } from 'react';
import Helper from './helper/helper';
import './helpers.css';

const Helpers = () => {
    const [helpers, setHelpers] = useState([]);
    const [darkMode, setDarkMode] = useState(false);

    useEffect(() => {
        const getData = async () => {
            const data = await fetch('helpers.json');
            setHelpers((await data.json()).helpers);
        };

        getData();
    }, []);

    const toggleDarkMode = () => {
        if (!darkMode) {
            setDarkMode(true);
            document.documentElement.style.setProperty('--color-background', '#121212');
            document.documentElement.style.setProperty('--color-dark', '#fefae0');
            document.documentElement.style.setProperty('--color-light', '#121212');
            document.documentElement.style.setProperty('--large-shadow', '');
            document.documentElement.style.setProperty('--hover-color', '#1a1a1a');
        } else {
            setDarkMode(false);
            document.documentElement.style.setProperty('--color-background', '#ffffff');
            document.documentElement.style.setProperty('--color-dark', '#000000');
            document.documentElement.style.setProperty('--color-light', '#ffffff');
            document.documentElement.style.setProperty('--large-shadow', '0px 32px 53px rgba(17, 17, 17, 0.08)');
            document.documentElement.style.setProperty('--hover-color', '#ffffff');
        }
    };

    return (<div className="Helpers"> 
        <div class="theme"><p onClick={toggleDarkMode}>Toggle {darkMode ? "Light" : "Dark"} Theme</p></div>
        {
            helpers.map((helper) => {
                return (<Helper {...helper}></Helper>)
            })
        }
    </div>);
};

export default Helpers;